
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;500;600&family=Mulish:wght@200;400;500;600;700&family=Nunito+Sans:ital,wght@0,200;0,300;0,600;0,700;1,400&display=swap');


* {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

body {
  background-color: lightgray;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.editor-block {
  background-color: white;
  width: 75%;
  height: 250px;
  overflow: AUTO;
  padding: 16px;
  border-radius: 5px;
}

.editor-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 16px;
}

.save-btn {
  background-color: greenyellow;
  border: none;
  color: black;
  font-size: 16px;
  border-radius: 5px;
  padding: 16px;
  margin-top: 16px;
  width: 75%;
  transition: 0.5s;
  cursor:pointer;
}

.save-btn:hover {
  scale: 1.05;
  color: darkgreen;
  letter-spacing: 8px;
}

.output {
  background-color: white;
  width: 75%;
  overflow: AUTO;
  padding: 16px;
  border-radius: 5px;
  margin-top: 16px;
}

.input-field {
  width: 75%;
  overflow: AUTO;
  padding: 16px;
  border-radius: 5px;
  margin-top: 16px;
  border: none;
  transition: .5s;
}

.hidden {
  display: none;
}

hr{
  border: 1px solid greenyellow;
  width: 100%;
  margin-top: 24px;
  margin-bottom: 24px;
}


input:focus{
  outline: none;
  scale: 1.05;
  transition: .5s;
}